import { css } from "@emotion/react";

import { applyResponsiveCss } from "../utils";

export const stack = css`
  display: grid;
  grid-template-rows: auto;

  & > * {
    min-width: 0;
  }
`;

export const align = applyResponsiveCss(
  (alignValue) =>
    css`
      justify-items: ${alignValue};
    `
);

export const stackGap = applyResponsiveCss(
  (gap) => css`
    row-gap: ${gap}px;
  `
);
