import React from "react";
import PropTypes from "prop-types";

import { responsivePropType } from "../utils";
import * as styles from "./styles";

export default function Row({
  gap,
  vGap,
  align = "stretch",
  wrap = true,
  wrapBefore,
  children,
}) {
  return (
    <div
      css={[
        styles.row,
        styles.rowWrap(wrap),
        styles.rowAlign(align),
        styles.rowGap(gap),
        styles.rowVGap(vGap),
        !wrapBefore ? null : styles.wrapBefore(wrapBefore),
      ]}
    >
      {React.Children.map(children, (child, index) =>
        !child ? null : (
          <div
            key={index}
            css={[styles.box, styles.boxGap(gap), styles.boxVGap(vGap)]}
          >
            {child}
          </div>
        )
      )}
    </div>
  );
}

Row.propTypes = {
  gap: responsivePropType(
    PropTypes.oneOfType([PropTypes.oneOf(["spaceBetween"]), PropTypes.number])
  ).isRequired,
  vGap: responsivePropType(
    PropTypes.oneOfType([PropTypes.oneOf(["spaceBetween"]), PropTypes.number])
  ),
  align: responsivePropType(
    PropTypes.oneOf([
      "start",
      "center",
      "end",
      "stretch",
      "flex-start",
      "flex-end",
    ])
  ),
  wrap: PropTypes.bool,
  wrapBefore: PropTypes.number,
  children: PropTypes.node.isRequired,
};
