import React from "react";
import PropTypes from "prop-types";

import { responsivePropType } from "../utils";
import * as styles from "./styles";

export default function Stack({ gap, align = "center", children }) {
  return (
    <div css={[styles.stack, styles.align(align), styles.stackGap(gap)]}>
      {children}
    </div>
  );
}

Stack.propTypes = {
  gap: responsivePropType(PropTypes.number).isRequired,
  align: responsivePropType(
    PropTypes.oneOf(["start", "center", "end", "stretch", "flex-start", "flex-end"])
  ),
  children: PropTypes.node.isRequired,
};
