import { css } from "@emotion/react";

import { applyResponsiveCss } from "../utils";

export const row = css`
  display: flex;
`;

export const rowWrap = (wrap) => css`
  flex-wrap: ${wrap ? "wrap" : "nowrap"};
`;

export const rowAlign = applyResponsiveCss(
  (align) => css`
    align-items: ${align};
  `
);

export const rowGap = applyResponsiveCss((gap) =>
  gap === "spaceBetween"
    ? css`
        justify-content: space-between;
      `
    : css`
        margin-left: ${-gap}px;
      `
);

export const rowVGap = (vGap) =>
  !vGap
    ? ""
    : css`
        margin-top: ${-vGap}px;
      `;

export const wrapBefore = (screenSize) => css`
  @media screen and (max-width: ${screenSize}px) {
    flex-direction: column;
  }
`;

export const box = css`
  display: flex;

  & > * {
    flex: 1;
  }
`;

export const boxGap = applyResponsiveCss((gap) =>
  gap === "spaceBetween"
    ? ""
    : css`
        margin-left: ${gap}px;
      `
);

export const boxVGap = (vGap) => css`
  margin-top: ${vGap}px;
`;
