import { css } from "@emotion/react";
import PropTypes from "prop-types";

export function applyResponsiveCss(atomicFunction) {
  return function responsiveCss(arg) {
    if (Array.isArray(arg)) {
      const [defaultValue, responsiveValues] = arg;
      const defaultStyle = atomicFunction(defaultValue);
      const responsiveStyles = Object.entries(responsiveValues)
        .sort((a, b) => b[0] - a[0])
        .map(
          ([key, value]) =>
            css`
              @media screen and (max-width: ${key}px) {
                ${atomicFunction(value)};
              }
            `
        );
      return [defaultStyle, responsiveStyles];
    }
    return atomicFunction(arg);
  };
}

export function responsivePropType(propType) {
  return PropTypes.oneOfType([
    propType,
    PropTypes.arrayOf(
      PropTypes.oneOfType([propType, PropTypes.objectOf(propType)])
    ),
  ]);
}
